import React from 'react';
import CardComponent from './card/card';
import { GoogleApiWrapper } from 'google-maps-react';
import MyComponent from './mapContainer';

type FruitLoopsProps = {
    list: Array<any>
}

function FruitLoops(props: FruitLoopsProps) {
    return (
        <div>
            {/*<div className="d-none d-lg-block">
                <MyComponent />
    </div>*/}

            <div className="text-center paddingT100">
                {props.list.map((value, index) => {
                    return <CardComponent item={value} key={index}></CardComponent>
                })}
            </div>
        </div>
    )
}

export default FruitLoops;