import React, { useState } from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import { View } from './enum-view';
import { Button, Modal } from "react-bootstrap";
import { Lang } from './lang/lang';

type MenuProps = {
  callback: any
}

const lang = new Lang();


const CategoryMenu = (props: MenuProps) => {

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);


  return (
    <>

      <Navbar className="categoryMenu">
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">

          <Nav className="m-auto">
            <Nav.Link href="#home" onClick={() => props.callback(View.Home)} >{lang.translate("_home")}</Nav.Link>
            <Nav.Link href="#nosaltres" onClick={() => props.callback(View.Home)}>{lang.translate("_us")}</Nav.Link>
            <Nav.Link href="#wigshops" onClick={() => props.callback(View.Category1)}>{lang.translate("_wigShops")}</Nav.Link>
            <Nav.Link href="#contruction" onClick={() => props.callback(View.Category1)}>{lang.translate("_contruction")}</Nav.Link>
            <Nav.Link href="#nutrition" onClick={() => props.callback(View.Category1)}>{lang.translate("_nutrition")}</Nav.Link>
            <Nav.Link href="#oncology" onClick={() => props.callback(View.Category1)}>{lang.translate("_psychooncology")}</Nav.Link>
            <Nav.Link href="#discounts" onClick={() => props.callback(View.Category1)}>{lang.translate("_discounts")}</Nav.Link>
            <Nav.Link href="#contact" onClick={() => props.callback(View.Home)}>{lang.translate("_contact")}</Nav.Link>
            <Nav.Link onClick={handleShow}>{lang.translate("_changeLanguage")}</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar >


      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
  <Modal.Title>{lang.translate("_changeLanguage")}</Modal.Title>
        </Modal.Header>
        <div className="centerButtons">
          <Button variant="primary" onClick={() => lang.changeLang("es")}>
            {lang.translate("_spanish")}
          </Button>
          <Button variant="primary" onClick={() => lang.changeLang("en")}>
          {lang.translate("_english")}
          </Button>
        </div>
      </Modal>



    </>


  )
}

export default CategoryMenu;