import React, { useEffect } from 'react';
import Rellax from "rellax";
import { Lang } from './lang/lang';
const $ = require('jquery');

function Home() {
    useEffect(() => {
        new Rellax("#header", {
            speed: 3,
            center: false,
            wrapper: null,
            round: true,
            vertical: true,
            horizontal: false
        });

        new Rellax(".home-photo", {
            speed: -5,
        });
    }, []);

    setTimeout(() => {
        $("#header-text").css("opacity", "1");
    }, 1000)


    const lang = new Lang();

    return (
        <div >
            <div className="home marginTHeader" style={{ marginTop: "0" }}>
                <div className="box home-photo">
                    <div className="centerBox" id="header">
                        <div className="title" data-aos='fade-zoom-in'>ONCOAID</div>
                        <div className="" id="header-text">
                            <p>{lang.translate("_home-header-1")}</p>
                            <p><u>Your cancer is our cancer</u></p>
                        </div>
                    </div>
                </div>

                <div className="box">
                    <div className="centerBox text-justify">
                        <h2 data-aos='fade-up'>What we do?</h2>
                        <p data-aos='fade-up'>In <span className="bigTitle">OncoAid</span> we are commited to help in every aspect surrounding the patient with cancer and his needs.</p>
                        <p data-aos='fade-up'>With more than <b>30 collaborations</b> with specialized shops and services, we pretend to be part of your disease.
                            Thus, <u><span className="bigTitle">your cancer is our cancer</span></u>. Our oncology specialized nurses will call you through video call/ call
                            to do an evaluation and recommend the best personalized option.</p>
                    </div>
                </div>

                <div className="box backgroundGrey">
                    <div className="text-justify centerBoxBig">
                        <div data-aos='fade-zoom-in' className="centerBox text-center">
                            <h2>Our Services</h2>
                            <p>Everyday we work hard to make life of our users better and happier</p>
                        </div>

                        <div className="row text-center home-services paddingT100">
                            <div data-aos='fade-zoom-in' className="col-md-4">
                                <img className="" src="https://static.tildacdn.com/tild3663-6162-4638-b264-376336656234/1.svg"></img>
                                <h3>Quality</h3>
                                <p>All our professionals have more than 5 years of legal experiences.</p>
                            </div>

                            <div data-aos='fade-zoom-in' className="col-md-4">
                                <img className="" src="https://static.tildacdn.com/tild3464-3434-4837-b033-653337643236/2.svg"></img>
                                <h3>Support</h3>
                                <p>We offer you professional consultation of our specialist in 30 minutes.</p>
                            </div>

                            <div data-aos='fade-zoom-in' className="col-md-4">
                                <img className="" src="https://static.tildacdn.com/tild3632-6665-4232-a636-306661333033/3.svg"></img>
                                <h3>Price</h3>
                                <p>Our prices are fixed for some standard services.</p>
                            </div>
                        </div>
                    </div >
                </div>


                {/*<img data-aos='fade-zoom-in' src="https://i.gyazo.com/5f9de5937ba5c352db07c8f53994eca3.jpg" className="w-100"></img>*/}
                <div className="box text-center">
                    <div data-aos='fade-zoom-in' className="centerBox">
                        <p className="opinio text-justify">"Lorem ipsum dolor sit amet, consectetur adipiscing elit. In a commodo dui. Proin cursus mollis dignissim. Donec facilisis, sapien id lacinia aliquam, velit enim auctor sem, ac ornare nunc turpis imperdiet metus."</p>
                        <img className="avatar" src="https://i.gyazo.com/08b21d885938c569824a69bbbc685258.jpg"></img>
                        <p className="font-weight-bold">Silvia</p>
                        <span className="h6" >Paciente</span>
                    </div>
                </div >
            </div>
        </div>
    )
}

export default Home;