import { Word } from './word'
import { translations } from './translations'

let $ = require('jquery');

export class Lang {

	private words: Array<Word>;

	constructor() {
		this.words = [];
		let wordsList: Array<string> = translations.split("[");
		wordsList = wordsList.map(word => word.split("]")[0]);

		wordsList.forEach(word => {
			word = word.substring(1, word.length - 1);
			const wordSplit: Array<string> = word.split('", "');
			this.newWord(wordSplit[0], wordSplit[1], wordSplit[2], wordSplit[3]);
		});
	}

	//Añade una palabra nueva al diccionario
	private newWord(name: string, esp: string, eng: string, cat: string): void {
		const word = new Word(name, esp, eng, cat)
		this.words.push(word);
	}

	//Devuelve la palabra con esa id
	private getWord(name: string): Word | null {
		const words: Array<Word> = this.words.filter(word => word.getId() == name);
		return words.length > 0 ? words[0] : null;
	}

	//Devuelve la palabra traduida
	public translate(name: string): string | null {
		var lang = window.navigator.language;
		if (localStorage.getItem("lang") != null)
			lang = localStorage.getItem("lang") + "";

		if(!["es", "en"].includes(lang))
			lang = "en";

		const word: Word | null = this.getWord(name);
		if (word == null) {
			console.log(name + " no existe");
			return null;
		}
		console.log(lang);

		return word.getTraduction(lang);
	}

	/*
	word: Objeto palabra
	ph: Si es placeholder
	*/
	private trad(word: Word, ph?: boolean): void | null {
		const name = word.getId();
		const wordName = this.translate(name);
		if (document.getElementById(name) == null)
			return null;

		$("[id=" + name + "]").each(function () {
			if (ph) {
				//@ts-ignore
				$(this).attr('placeholder', wordName);
				return;
			}
			//@ts-ignore
			$(this).html(wordName);

		});
	}

	/*private manualChange(): void {
		this.changeLang();
	}

	private changeLang(): void {
		this.translateAll();
	}*/

	public translateAll(): void {
		console.log("translating all");
		this.words.forEach(word => this.trad(word));
	}

	/*private translateList(list): void {
		list.forEach(word => this.translate(word));
	}*/

	public tradWord(id: string, ph?: boolean): boolean {
		var result = this.words.filter(obj => obj.getId() === id)[0];

		if (result == undefined) {
			console.log("Error: Incorrecto id Translate: " + id);
			return false;
		}

		this.trad(result, ph);

		return true;
	}

	public changeLang(lang: string) {
		localStorage.setItem("lang", lang);
		window.location.href = "./";
	}
}