export class Word {
    private _id: string;
    private es: string;
    private en: string;
    private cat: string;

    constructor(name: string, es: string, en: string, cat: string) {
        this._id = name;
        this.es = es;
        this.en = en;
        this.cat = cat;
    }

    public getId(): string {
        return this._id;
    }

    public getTraduction(id: string | null): string | null {
        if(id == null)
            return null;

        return this[id];
    }
}