import React from 'react';
import { Card } from '../card-props';
import { GeoAlt, TelephoneFill, Clock, Globe } from 'react-bootstrap-icons';

type CardProps = {
    item: Card
}

function CardComponent(props: CardProps) {
    return (
        /*<div className="card">
            <img src={props.src} alt="" ></img>
            <h1>{props.title}</h1>

        </div>*/

        <div className="card">
            <div className = "card-discount">{props.item.discount}% discount</div>
            <img className="card-img-top" src={props.item.src} alt="Card image cap" />
            <div className="card-body">
                <h5 className="card-title">{props.item.title}</h5>


                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star checked"></span>
                <span className="fa fa-star"></span>


                <p className="card-text my-4">{props.item.description}</p>

                <div className="text-left">
                    <p className="card-text">
                        <GeoAlt />
                        <small className="text-muted"> {props.item.dir}</small>
                    </p>

                    <p className="card-text">
                        <Clock />
                        <small className="text-muted"> Opens at: {props.item.open}</small>
                    </p>

                    <p className="card-text">
                        <TelephoneFill />
                        <small className="text-muted"> {props.item.telephone}</small>
                    </p>

                    <p className="card-text">
                        <Globe />
                        <small className="text-muted"> {props.item.web}</small>
                    </p>
                </div>
            </div>
        </div>
    )
}

export default CardComponent;